.line {
    position: relative;
    width: 50%;
    height: 3px;
    margin: 0 auto;
    overflow: hidden;
  }
  
  .line::before {
    content: "";
    position: absolute;
    width: 200%;
    height: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(90deg, rgba(65, 64, 66, 1) 0%, rgba(3, 169, 196, 1) 50%, rgba(65, 64, 66, 1) 90%, rgba(65, 64, 66, 1) 100%);
    animation: gradientMove 3s cubic-bezier(0.1, 0.3, 0.4, 0.2) infinite;
  }
  
  @keyframes gradientMove {
    0% {
      transform: translateX(-50%);
    }
    50% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(50%);
    }
  }
  
  
  
