.body{
    display: flex;
}

.personal-info-section{
    
    min-width: 18rem;
    background-color: #282c341d;
    
}

.personal-info-section .photo-section{
    width: 200px;
    height: 200px;
    margin: 40px auto;
    border-radius: 100%;
    overflow: hidden;
}
.personal-info-section .slash, .exp-container .slash{
    width: 80%;
    margin: 20px auto;
    border-top: solid 1px #282c3476;
}
.personal-info-section .small-slash{
    width: 80%;
    margin: 6px auto;
    border-top: dashed 1px #282c3476;
}

.personal-info-section .person-details ul{
    padding: 20px 0;
}
.personal-info-section .person-details ul li{
    list-style: none;
    padding: 0 0 0 20%;
    text-align: left;
}
.personal-info-section .person-details ul li .title{
    color: #282c34df;
    padding-bottom: 4px;
}
.personal-info-section .person-details ul li h5{
    color: #282c34df;
}
.personal-info-section .photo-section img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-repeat: no-repeat;
}

.exp-container{
    padding: 30px 40px;
    width: 100%;
    line-height: 1.3;
}
.exp-container .info-section {
   margin-bottom: 50px;
}
.exp-container .info-section .title h1{
    text-align: left;
}
.exp-container .info-section .title .slash{
    width: 100%;
}
.exp-container .info-section .content-section{
    text-align: left;
    margin-bottom: 30px;
}

