*{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

h1{
  color: white;
  font-size: 40px;
}

.h1-on{
  color: #414042;
  font-size: 40px;
}

h3{
  color: #cccccc8e;
  font-size: 13px;
}

.h3-on{
  color: #414042;
  font-size: 13px;
}

.App {
  text-align: center;
  box-shadow: -0.1em 0 1em rgba(0, 0, 0, 0.471), -0.1em 0 1em rgb(0, 0, 0, 0.471);;
  margin: 20px 200px 100px 200px;
}

.App-logo {
  height: 5vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #00c8ff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/* Smartphones (portrait and landscape) ----------- */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 459px) {
  .App{
    margin: 10px 10px 30px 10px;;
  }
  .App .container {
    padding: 20px 0;
  }
  .body .personal-info-section {
    min-width: 15rem;
  }
  .contact-section {
    flex-direction: column;
    align-items: center;
  }
  .contact-section .phone {
    border-left: none;
    border-right: none;
  }
  .contact-section .contact {
    padding: 10px 0px;
  }
  .body{
    flex-direction: column;
    padding: 0 20px;
  }
  .body .exp-container {
    padding: 20px 0;
  }
  .personal-info-section .person-details ul li {
    padding-left: 20px!important;
    padding-right: 20px!important;
  }
  .personal-info-section .small-slash {
    width: 100%!important;
}
}


/* iPads (portrait and landscape) ----------- */
@media only screen
and (min-device-width: 460px)
and (max-device-width: 789px) {
  .App{
    margin: 20px 30px 50px 30px;
  }
  .body{
    flex-direction: column;
    padding: 0 30px;
  }
  .body .exp-container {
    padding: 30px 0;
  }
  .personal-info-section .person-details ul li {
    padding-left: 30px!important;
    padding-right: 30px!important;
  }
  .personal-info-section .small-slash {
    width: 100%!important;
}
}



/* iPads (portrait) ----------- */
@media only screen
and (min-device-width: 790px)
and (max-device-width: 1024px) {
  .App{
    margin: 20px 50px 50px 50px;
  }
  .body{
    flex-direction: column;
    padding: 0 50px;
  }
  .body .exp-container {
    padding: 30px 0;
  }
  .personal-info-section .person-details ul li {
    padding-left: 100px!important;
  }
}